<h1 id="数据统计与分析" tabindex="-1"><a class="header-anchor" href="#数据统计与分析" aria-hidden="true">#</a> 数据统计与分析</h1>
<p>获取小程序概况趋势：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">data_cube</span><span class="token operator">-></span><span class="token function">summaryTrend</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'20170313'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'20170313'</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>开始日期与结束日期的格式为 yyyymmdd。</p>
<h2 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h2>
<blockquote>
<ul>
<li><code>summaryTrend(string $from, string $to);</code> 概况趋势</li>
<li><code>dailyVisitTrend(string $from, string $to);</code> 访问日趋势</li>
<li><code>weeklyVisitTrend(string $from, string $to);</code> 访问周趋势</li>
<li><code>monthlyVisitTrend(string $from, string $to);</code> 访问月趋势</li>
<li><code>visitDistribution(string $from, string $to);</code> 访问分布</li>
<li><code>dailyRetainInfo(string $from, string $to);</code> 访问日留存</li>
<li><code>weeklyRetainInfo(string $from, string $to);</code> 访问周留存</li>
<li><code>monthlyRetainInfo(string $from, string $to);</code> 访问月留存</li>
<li><code>visitPage(string $from, string $to);</code> 访问页面</li>
<li><code>userPortrait(string $from, string $to);</code> 用户画像分布数据</li>
</ul>
</blockquote>
